import axios from 'axios'
import Toast from 'vant/lib/toast'

const instance = axios.create({
    baseURL: process.env.BASE_URL,
    headers: {
        'Content-Type': 'application/json'
    }
})

/***
 * 获取导航路线
 * @param data
 * @param data.StartCoordinate 起点
 * @param data.GoalCoordinate 终点
 * @param data.Vehicle 船舶
 * @return {*}
 */
export function getNavigationRoute(data) {
    const url = `/route`

    Toast({
        position: 'center',
        type: 'loading',
        message: '线路规划中，请稍等 ...',
        duration: 0
    })

    return instance.post(url, data).then(res => {
        let {data: {code, msg, data, result}} = res

        if (code === -1 || code === '-1' || code === '0' || code === 0) {
            Toast(msg)
            return Promise.resolve(false)
        }

        Toast.clear()

        try {
            result = JSON.parse(data)
        } catch (e) {
            result = data

            console.log(e)
        }

        return Promise.resolve(result)
    })
}


/***
 * 获取导航路线
 * @param data
 * @param data.centerPoint.longitude
 * @param data.centerPoint.latitude
 * @param data.distance  范围
 * @param data.types  搜索类别
 * @return {*}
 */
export function getNavigationPoint(data) {
    const url = `/search/geofilt`

    return instance.post(url, data).then(res => {
        let {data: {code, msg, data, result}} = res

        if (code === -1 || code === '-1' || code === '0' || code === 0) {
            Toast(msg)
            return Promise.resolve([])
        }

        return Promise.resolve(data)
    })
}

export function getNavigationals(data) {
    const url = `/search/channel`

    return instance.post(url, data).then(res => {
        let {data: {code, msg, data, result}} = res

        if (code === -1 || code === '-1' || code === '0' || code === 0) {
            Toast(msg)
            return Promise.resolve([])
        }

        return Promise.resolve(data)
    })
}



