<template>
    <mu-appbar class="navigation" :style="style" :title="title">
        <mu-button slot="left" color="#fff" @click="handlerClickBack" icon>
            <mu-icon size="25" value="navigate_before"/>
        </mu-button>
        <mu-button slot="right" color="#fff" icon>
            <mu-icon size="25" value="0"/>
        </mu-button>
    </mu-appbar>
</template>

<script>
    export default {
        name: "navigate",
        data() {
            return {}
        },
        props: {
            title: {
                type: String,
                default: ''
            },
            position: {
                type: String,
                default: 'relative'
            },
            color: {
                type: String,
                default: '#fff'
            },
            backgroundColor: {
                type: String,
                default: ''
            },
            shadow: {
                type: Boolean,
                default: false
            },
            back: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            boxShadow() {
                return this.shadow ? '0 0 7px -1px rgba(0, 0, 0, .33)' : 'none'
            },
            style() {
                const style = {
                    position: this.position,
                    color: this.color,
                    boxShadow: this.boxShadow
                }

                if (this.backgroundColor) {
                    style.backgroundColor = this.backgroundColor
                } else {
                    style.backgroundImage = `linear-gradient(45deg, #3899D7 62%, #288DCD 35%)`
                }

                return style
            }
        },
        methods: {
            handlerClickBack() {
                if (!this.back) {
                    this.$router.back()
                }
                this.$emit('back')
            }
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .navigation
        position relative
        top 0
        left 0
        right 0
        z-index 300
        height 46px
        transition all .5s
        .back
            font-size $font-size-ss
        >>> .mu-appbar-title
            height 46px
            line-height 46px
            text-align center
            font-size $font-size-mm
</style>



